import { Table, Tag } from 'antd'

const status = ['Completed', 'In Progress', 'Open', 'On Hold']
const columns = [
  {
    title: 'Task Name',
    dataIndex: 'name',
  },
  {
    title: 'Status',
    dataIndex: 'status',
    filters: status.map((s) => ({ text: s, value: s })),
    onFilter: (value, record) => record.status.indexOf(value) === 0,
    render: (status) => (
      <Tag color={status === 'In Progress' ? 'geekblue' : status === 'Open' ? 'blue' : status === 'On Hold' ? 'volcano' : 'green'} key={status}>
        {status.toUpperCase()}
      </Tag>
    ),
  },
]
const data = [...Array(12)].map((i, d) => ({
  key: `${d}`,
  name: `Task ${d}`,
  status: status[~~(Math.random() * 4)],
}))
console.log(data)

function onChange(pagination, filters, sorter, extra) {
  console.log('params', pagination, filters, sorter, extra)
}

function Details() {
  return (
    <div>
      <Table columns={columns} dataSource={data} onChange={onChange} />;
    </div>
  )
}

export default Details
